import React, { Component } from 'react'
import Draggable, {DraggableCore} from 'react-draggable'; // Both at the same time

class Ticket extends Component {
    constructor(props){
        super(props)
        this.randomBetween = this.randomBetween.bind(this)
    }

    componentWillMount() {
        this.style = {
            right: this.randomBetween(0, window.innerWidth - 300, 'px'),
            top: this.randomBetween(0, 3000 - 300, 'px'),
            transform: `rotate(${this.randomBetween(-25, 25, 'deg')})`
        }
    }
    randomBetween(x,y,s) {
        return x + Math.ceil(Math.random() * (y-x)) + s
    }

    render() {
        return (
            <Draggable>
            <div className="ticket" style={this.style}>
                <dl>
                    <dt>Artist</dt>
                    <dd>{this.props.children.artist}</dd>
                    <dt>Venue</dt>
                    <dd>{this.props.children.venue}</dd>
                    <dt>City</dt>
                    <dd>{this.props.children.city}</dd>
                    <dt>Date</dt>
                    <dd>{this.props.children.date}</dd>
                </dl>
                <div className={this.props.children.size}>
                    <img src={require('./images/' + this.props.children.image)} alt="{this.props.children.artist}" />
                </div>
            </div>
            </Draggable>
        )
    }
}

export default Ticket


